import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick'

import VueResource from 'vue-resource'

import store from './vuex/Store.js'

import Data from '@/util/Data'

import Validate from '@/util/Validate'

import Util from '@/util/Util'

import infiniteScroll from 'vue-infinite-scroll'

import metaInfo from 'vue-meta'

import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

FastClick.attach(document.body)
Vue.use(VueResource)
Vue.use(store)
Vue.use(Data)
Vue.use(Validate)
Vue.use(Util)
Vue.use(infiniteScroll)
Vue.use(metaInfo)
Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
metaInfo: {
title: 'Eltur', // set a title
titleTemplate: '%s', // title is now "My Example App - Yay!"
meta: [

{name: 'viewport', content: 'width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0'},
{name: 'apple-mobile-web-app-capable', content: 'yes'},
{name: 'apple-mobile-web-app-status-bar-style', content: 'black'},
{name: 'format-detection', content: 'telephone=no'},
{name: 'format-detection', content: 'email=no'}
],
htmlAttrs: {
lang: 'zh',
amp: undefined 
}
},
router,
store,
template: '<App/>',
components: { App }
})



